import React, { FC } from 'react';
import { createUseStyles } from 'react-jss';

const useStyles = createUseStyles((theme: any) => ({
    progressiveBar: {
        composes: 'flexRow justifyCenter red',
        backgroundColor: '#F4F9FE',
        borderRadius: 15,
        overflow: 'hidden',
        width: (width: number) =>{
            console.log('width: ', width);
            return width || '100%'
        },
        height: 10,
    },
    color: {
        backgroundColor: (color: string) => 'color',
        width: (percent: number) => percent + '%',
    }
}));

type Props = { color: string; percent: number; width: number };
const ProgressiveBar: FC<Props> = ({ color, percent, width }) => {
    const classes = useStyles({ color, percent, width });
    return (
        <div className={classes.progressiveBar}>
            <div className={classes.color} />
        </div>
    );
};

export default ProgressiveBar;
